<script>
    import { eventBus } from '../../main'
    export default {
        props: ['user', 'isMobile'],
        data () {
            return {

                dialog: false,
                institution: '',
                course: '',
                selectedUserSubjects: [],
                universitySubjects: [],
                faculty: '',
                department: '',
                courseNivel: '',
                level: '',
                userClass: '',
                userSchool: '',
                educationState: '',
                showAcademicProcess: false,
                isInicializing: false,
                canceled: false,
            }
        },
        mounted ()
        {

//            this.$store.dispatch('getAllTemData')

            //Tratando o evento para abertura da dialog, chamado no dialogs/OtherProfileEditSubjectsDialog.vue
            eventBus.$on('openAcademicDialog', () => {
                this.dialog = true
            })
        },
        computed: {
            error () {
                return this.$store.getters.error
            },
            loading () {
                return this.$store.getters.loading
            },
            userAuth () {
                return this.$store.getters.userAuth
            },
            isUserAuth () {
                return this.userAuth !== null && this.userAuth !== undefined
            },

            isPrimaryLevel () {
               return this.level === 'Primário'
            },
            isSecundaryLevel () {
                return this.level === 'Secundário'
            },
            isTenicalLevel () {
                return this.level === 'Técnico'
            },
            isMasterLevel () {
                return this.level === 'Superior'
            },
            isOtherLevel () {
                return this.level === 'Outro'
            },
            studiesLevels () {
                return this.$store.getters.getStudiesLevels
            },
            primaryClasses () {
                return this.$store.getters.getPrimaryClasses.slice(1-4) //retirando as primeiras 4 classes.
            },

            secundaryClasses () {
                return this.$store.getters.getSecundaryClasses
            },

            tecnicalDegrees () {
                return this.$store.getters.getTecnicalDegrees
            },

            primarySubjects2ndDegree () {
                return this.$store.getters.getPrimarySubjects2ndDegree
            },

            secundarySubjects () {
                return this.$store.getters.getSecundarySubjects
            },

            otherSubjects () {
                return this.$store.getters.getOtherSubjects
            },

            schools () {
                return this.$store.getters.getSchools
            },

            institutions () {
                return this.$store.getters.getInstitutions
            },

            otherCourses () {
                return this.$store.getters.getOtherCourses
            },

            universities () {
                return this.$store.getters.getUniversities
            },

            educationStates () {
                return this.$store.getters.getEducationState
            },

            faculties () {
                return this.$store.getters.getFaculties
            },
            facultyNames () {
                return this.$store.getters.getFacultieNames
            },

            departments () {
                return this.$store.getters.getDepartments
            },
            tecnicalCourses () {
                return this.$store.getters.getTecnicalCourses
            },
            courseNames () {
                return this.$store.getters.getUEMCourses
            },

            subjects () {
                return this.$store.getters.getSubjects
            },

            isUser () {
                return this.user !== null && this.user !== undefined
            },
            isMyProfile () {
                return this.userIsAuth && this.isUser && this.user.id === this.userAuth.id
            }
        },
        watch: {

            level (value)
            {
                if (!this.isInicializing)
                {
                    this.userClass = this.userAuth && this.userAuth.Educations && this.userAuth.Educations.level === value ? this.userAuth.Educations.userClass : ''
                    this.userSchool = this.userAuth && this.userAuth.Educations && this.userAuth.Educations.level === value ? this.userAuth.Educations.userSchool : ''
                    this.educationState = this.userAuth && this.userAuth.Educations && this.userAuth.Educations.level === value ? this.userAuth.Educations.educationState : ''
                }
                this.isInicializing = false
            },
            isPrimaryLevel (value) {
                if (value !== null &&  value)
                {
                    this.selectedUserSubjects = this.primarySubjects2ndDegree
                }
            },

            isSecundaryLevel (value) {
                if (value !== null && value)
                {
                    this.selectedUserSubjects = this.secundarySubjects
                }
            },
            loading (value) {

                if (!value && !this.error && this.showAcademicProcess && !this.canceled) {

                    let text = "Os seus dados academicos foram actualizados com sucesso!"
                    let color = 'green'
                    eventBus.$emit('showSnackbar', text, color, 5000); //Esse evento eh tratado no ficheiro App.vue, eh la onde foi montado o componente snackbar
                    this.showAcademicProcess = false
                    this.dialog = false
                }
            },
            dialog (value)
            {
                if (value) {

                    this.inicializeUserInfo()
                }
                else //Entao a dialog esta a fechar
                {

                    //Fazer get do user.
//                    this.$store.dispatch('updateTheUserInArray', this.userAuth)
                    this.$store.dispatch('setLoading', false)
                    this.$store.dispatch('getAllUsers')
                    this.canceled = true
                }

            },
            course (course)
            {
                if (this.educationState === 'em curso')
                {
                    this.getCourseSubjects (course)
                }

            },
            educationState (state)
            {
                if (state === 'em curso')
                {
                    this.getCourseSubjects (this.course)
                }

            }
        },
        methods: {

            onSelectedItem (item)
            {
//                console.log(item)

                if (item.type === 'Nivel') { this.level = item.name }
                else if (item.type === 'School') { this.userSchool = item.name }
                else if (item.type === 'Institution') { this.userSchool = item.name }
                else if (item.type === 'OtherCourse') { this.course = item.name }
                else if (item.type === 'OtherSubject') { this.selectedUserSubjects.push(item.name) }
                else if (item.type === 'PrimarySubject') { this.selectedUserSubjects.push(item.name) }
                else if (item.type === 'Universities') { this.institution = item.name }
                else if (item.type === 'Faculty') { this.faculty = item.name }
                else if (item.type === 'Course') { this.course = item.name }
                else if (item.type === 'Department') { this.department = item.name }
                else if (item.type === 'UniversitySubject') { this.selectedUserSubjects.push(item.name) }
                else if (item.type === 'TecnicalCourses') { this.course = item.name }

            },
            educationsNivels (course)
            {
                if (course)
                {
                    if (course.toLowerCase().includes('engenharia')) return ['1º ano', '2º ano', '3º ano', '4º ano', '5º ano']
                    if (course.toLowerCase().includes('medicina')) return ['1º ano', '2º ano', '3º ano', '4º ano', '5º ano', '6º ano']
                }
                return ['1º ano', '2º ano', '3º ano', '4º ano']
            },
            onSave () {

                this.canceled = false
                let user = this.userAuth

                let today = new Date()
                user.Educations = {
                    educationState: this.educationState, //Estado da educacao: Em curso, desistido, concluido,...
                    level:          this.level,     //Nivel ou ensino em que ele esta afecto: Primario, Secundario, Tecnico, Superior, Outro
                    institution:    this.changeBeforeSave(this.institution),   //Caso estiver no ensino: Superior, Tecnico, Outro
                    userSchool:     this.changeBeforeSave(this.userSchool),    //Caso estiver no ensino: Primario, Secundario
                    userClass:      this.userClass,     //Caso estiver no ensino Primario ou secundario: como explicador so pode ser aprovado, se estiver de 5-12 classes.
                    course:         this.changeBeforeSave(this.course),        //O curso que o explicador frenquenta.
                    faculty:        this.changeBeforeSave(this.faculty),       //A faculdade em que ele esta afecto
                    department:     this.changeBeforeSave(this.department),    //Departamento em que ele esta afecto
                    courseNivel:    this.courseNivel,         //Nivel dele caso estiver no ensino superior: de 1 a 8 ano dependendo do curso.
                    subjects:       this.changeBeforeSave(this.selectedUserSubjects), //As disciplinas que ele frenquenta neste semestre.
                    created_at:     user.Educations && user.Educations.created_at? user.Educations.created_at : today,
                    updated_at:     today
                }
                this.showAcademicProcess = true

                this.$store.dispatch('updateOnlineUserData', user)
//
                // Chamar o metodo que vai gravar as disciplinas
//                this.saveNewSubjectsToDB()
//                this.saveFacultieToDB()
//                this.saveDepartmentToDB()

            },

            changeBeforeSave (itemName) {
                if (itemName) //Validando, para ter certeza quel algo foi passado.
                {
                    //Caso tenho sido passado uma string
                    if( typeof itemName === 'string' ) {
                        return this.$store.getters.getTempItemIdByName(itemName)
                    }

                    let items = []
                    //caso seja um arrary
                    itemName.forEach((item) => {
                        items.push(this.$store.getters.getTempItemIdByName(item))
                    })

                    return items
                }
            },

            changeBeforeShow (itemId) {
                if (itemId)
                {

                    //Caso tenho sido passado uma string
                    if (typeof itemId === 'string') {
                        return this.$store.getters.getTempItemNameById(itemId)
                    }

                    let items = []
                    //caso seja um arrary
                    itemId.forEach((item) => {
                        items.push(this.$store.getters.getTempItemNameById(item))
                    })

                    return items
                }
            },
            getCourseSubjects (course) {
                let vm = this
                setTimeout(() => {

                    vm.universitySubjects = this.$store.getters.getInternal_APISubjects (course)
//                    console.log('disciplinas buscadas', vm.universitySubjects)

                }, 1000)
//                return this.$store.getters.getInternal_APISubjects (course)

            },

            saveNewSubjectsToDB () {

                let vm = this
                this.subjectNames.forEach(function (selectSub) {

                    vm.$store.dispatch('saveSubject', {
                        name: selectSub,
                        creatorId: vm.userAuth.id,
                        created_at: new Date(),
                        confirmed: false
                    })
                })


            },

            saveDepartmentToDB () {

                if (this.department !== '')
                {
                    //gravar faculdade
                    this.$store.dispatch('saveDepartment', {
                        name: this.department,
                        creatorId: this.userAuth.id,
                        created_at: new Date(),
                        confirmed: false
                    })
                }

            },
            onCancel () {
                this.showAcademicProcess = false
                this.inicializeUserInfo()
                this.$store.dispatch('setLoading', false)
                this.canceled = true
                this.dialog = false
            },

            onDismissed () {
                this.$store.dispatch('clearError')
                this.$store.dispatch('setLoading', false)
            },

            inicializeUserInfo () {

                if (!this.isUserAuth) return //se nao estiver autenticado nao precisamos de inicializar

                const education = this.isUserAuth ? this.userAuth.Educations : {}

                if (!education) return //se o user nao tiver registado ainda os dados da educacao nao precisamos de inicializar

                this.isInicializing = true

                this.level = this.isUserAuth && education && education.level ? education.level : ''
                this.educationState = this.isUserAuth && education && education.educationState ? education.educationState : ''
                this.institution = this.isUserAuth && education && education.institution ? this.changeBeforeShow(education.institution) : ''
                this.userSchool = this.isUserAuth && education && education.userSchool ? this.changeBeforeShow(education.userSchool) : ''
                this.userClass = this.isUserAuth && education && education.userClass ? education.userClass : this.userClass
                this.course = this.isUserAuth && education && education.course ? this.changeBeforeShow(education.course) : ''
                this.faculty = this.isUserAuth && education && education.faculty ? this.changeBeforeShow(education.faculty) : ''
                this.department = this.isUserAuth && education && education.department ? this.changeBeforeShow(education.department) : ''
                this.courseNivel = this.isUserAuth && education && education.courseNivel ? education.courseNivel : ''

//                Implementacao temporaria, ao mudar para id, mudar isso.
                this.selectedUserSubjects = education && education.subjects ? this.changeBeforeShow(education.subjects)  : []

            },

            onHelp () {
                this.dialog = false
                this.$router.push('/help')
            }
        }
    }
</script>


