<template>
    <v-layout row justify-center>
        <v-dialog v-model="dialog" scrollable max-width="300px">
            <!--<v-btn slot="activator" color="primary" dark>Open Dialog</v-btn>-->
            <v-btn slot="activator" class="mt-4" icon="" :title="title" color="blue"><v-icon>add</v-icon></v-btn>
            <v-card>
                <v-card-title>{{ title }}</v-card-title>
                <v-divider></v-divider>
                <v-card-text style="height: 300px;">

                    <v-layout>
                        <v-flex xs12 class="">
                            <v-text-field
                                    :label="label"
                                    :hint="hint"
                                    v-model="itemName"
                                    :rules="[
                                            () => !!itemName || 'Este campo é obrigatório',
                                            () => !!itemName && itemName.length <= 75 || 'Excedeu o limite de 75 caracteres',
                                        ]"
                                    required
                            ></v-text-field>
                        </v-flex>
                    </v-layout>
                    <v-layout v-if="itemName.length > 2 && similarItems(itemName).length">
                        <small>
                            Resultados semelhantes encontrados
                            <a v-if="!canShowSimilarItems" href="#!" @click="canShowSimilarItems = !canShowSimilarItems">Ver</a>
                            <a v-else="" href="#!" @click="canShowSimilarItems = !canShowSimilarItems">Ocultar</a>
                        </small>
                    </v-layout>
                    <!--Reportar aqui os erros-->
                    <v-layout v-if="canShowSimilarItems && itemName.length > 2 && similarItems(itemName).length" class="">
                        <v-flex>

                            <v-card-text class="ma-0 pl-0 pr-0 existing-items">
                                <!--<v-radio-group v-model="existingSelected" >-->

                                    <template v-for="item in similarItems(itemName)">
                                        <v-checkbox :label="item" v-model="existingSelected" :value="item"></v-checkbox>
                                    </template>

                                <!--</v-radio-group>-->
                            </v-card-text>

                        </v-flex>
                    </v-layout>

                </v-card-text>
                <v-divider></v-divider>
                <v-card-actions>
                    <v-btn color="blue darken-1" flat @click.native="dialog = false">Fechar</v-btn>
                    <v-btn
                        :disabled="!formIsValid || loading"
                        :loading="loading"
                        color="blue darken-1"
                        flat
                        @click.native="onSave()">
                        Salvar

                        <span slot="loader" class="custom-loader">
                            <v-icon light>cached</v-icon>
                        </span>
                    </v-btn>
                    <!--<v-btn-->
                            <!--flat-->
                            <!--@click="onSave"-->
                            <!--class="green&#45;&#45;text"-->
                            <!--:disabled="loading"-->
                            <!--:loading="loading"-->
                    <!--&gt;-->
                        <!--Salvar-->

                        <!--<span slot="loader" class="custom-loader">-->
                            <!--<v-icon light>cached</v-icon>-->
                        <!--</span>-->
                    <!--</v-btn>-->
                </v-card-actions>
            </v-card>
        </v-dialog>
    </v-layout>
</template>
<script>

    import { eventBus } from '../../main'
    export default {
        props: ['type', 'label', 'hint', 'title', 'items', 'userAuth', 'indicator'],
        data () {
            return {
                dialog: false,
                thisProcessing: false,
                canShowSimilarItems: false,
                existingSelected: '',
                savedObject: null,
                itemName: '',
                didUserSawSimilars: false,
                canceled: false,

            }
        },
        computed :
        {
            error () {
                return this.$store.getters.error
            },
            loading () {
                return this.$store.getters.loading
            },
            tempData () {
                return this.$store.getters.getTempData
            },
            formIsValid () {
                return (
                    this.itemName.length > 2 && this.itemName.length <= 75
                )
            },

        },
        watch: {

            loading (value) {

                if (!value && !this.error && this.thisProcessing && !this.canceled) {

                    let text = "Gravado com sucesso!"
                    let color = 'green'

                    if (this.indicator)
                    {
                        this.savedObject.indicator = this.indicator
                    }

                    this.$emit('selectedItem', this.savedObject) //Evento tratado no ficheiro OtherProfileEditAcademicDialog.vue
                    eventBus.$emit('showSnackbar', text, color, 5000); //Esse evento eh tratado no ficheiro App.vue, eh la onde foi montado o componente snackbar
                    this.thisProcessing = false
                    this.dialog = false
                }
            },
            dialog ()
            {
                this.existingSelected = ''
            },
            canShowSimilarItems (value)
            {
                if (value)
                {
                    this.didUserSawSimilars = true
                }
            },
        },
        methods: {

            similarItems (str)
            {
                return this.items.filter((i) => {

                    let textoI = this.$store.getters.eliminaAcentos(i)
                    let textoPesqu = this.$store.getters.eliminaAcentos(str)

                    return textoI.includes(textoPesqu)
                })
            },
            onSave ()
            {

                if (this.existingSelected)
                {
                    this.subjsExistInTemp (this.existingSelected, this.type) //verificar se pode-se gravar se for de universidade

                    //1. Emitir evento com esse item selecionado
                    const object = {
                        id:                 '0',
                        saverUserId:        this.userAuth.id,
                        name:               this.existingSelected,
                        type:               this.type,
                        confirmed:          false,
                        confirmerUserId:    '',
                        indicator:          this.indicator,
                    }

                    this.$emit('selectedItem', object) //Evento tratado no ficheiro OtherProfileEditAcademicDialog.vue
                    this.dialog = false

                    return
                }

                this.saveTempData ()

            },
            subjsExistInTemp (name, type)
            {
                if (type === 'UniversitySubject')
                {
                    const s = this.tempData.find((t) => {
                        return t.name === name
                    })
                    if (s === undefined)
                    {
                        console.log('Disciplina da Universidade nao encontrada, vai gravar...')
                        this.saveTempData ()
                    }
                }

            },
            trocaInteligente ()
            {
                //1. Se tiver selecionado disci e for da univ gravamos essa mesma.
                if (this.type === 'UniversitySubject' && this.existingSelected)
                {
                    return this.existingSelected
                }

                //2. mplementar aqui algoritmo inteligente.
                let foundItem = this.similarItems(this.itemName)[0]

                let textoAnt = this.$store.getters.eliminaAcentos(foundItem)
                let textoNovo = this.$store.getters.eliminaAcentos(this.itemName)

                if ((this.similarItems(this.itemName).length === 1 && textoAnt === textoNovo))
                {

                    console.log('trocou-se: ', this.itemName, 'por ', foundItem)
                    console.log('O nome final é: ', foundItem)
                    return foundItem //A disciplina registada antess eh que sera retornada.

                }

                console.log('Nenhuma troca foi feita')
                return this.itemName


            },
            saveTempData ()
            {

                if (!this.didUserSawSimilars && (this.similarItems(this.itemName).length > 0))
                {
                    let text = 'Por favor, veja os resultados sememlhantes encontrados. Talvez esse nome já tenha sido registado antes apenas selecione se existir.'
                    let color = 'red'
                    eventBus.$emit('showSnackbar', text, color, 8000); //Esse evento eh tratado no ficheiro App.vue, eh la onde foi montado o componente snackbar
                    this.thisProcessing = false
                    return
                }

                let name = this.trocaInteligente () //O nome disciplina sera trocado de forma inteligente, para um que ja tinha sido registado.

                const obj = {
                    id:                 '0',
                    saverUserId:        this.userAuth.id,
                    name:               name,
                    previousName:       this.itemName, //colocar aqui o nome anterior depois de fazer alguma alteracao na disciplina
                    type:               this.type,
                    confirmed:          false,
                    confirmerUserId:    '',
                    created_at:         new Date(),
                    updated_at:         '',
                }

                this.thisProcessing = true
                this.savedObject = obj
                this.$store.dispatch('saveTempData', obj)
            },
        }
    }

</script>
<style>
    .existing-items .input-group__details {
         min-height: 0px;
        /*background: red;*/
    }
    .existing-items .input-group label {
        font-size: 8pt;
    }
</style>


